<template>
    <div class="addDetail-container">
        <head-top head-title="地址搜索" goBack="true"></head-top>
        <section class="">
            <div class="add-detail">
                <input type="text" placeholder="请输入小区/写字楼/学校等" v-model="inputAdress">
                <button @click="inputThing">确定</button>
            </div>
            <div class="warnpart">
                为了满足商家的送餐要求，建议您从列表中选择地址
            </div>
            <div class="point" v-show="warning">
                <p>找不到地址？</p>
        		<p>请尝试输入小区、写字楼或学校名</p>
        		<p>详细地址（如门牌号）可稍后输入哦。</p>
            </div>
            <ul class="poisearch-container" v-show="adresslist">
                <li v-for="(item,index) in adresslist" :key="item.id" @click="listClick(index)">
                    <p>{{item.name}}</p>
        			<p>{{item.address}}</p>
                </li>
            </ul>
        </section>
    </div>
</template>

<script>
import {mapState,mapMutations} from 'vuex'
import headTop from '../../../../../../components/header/header'
import {searchNearby} from '../../../../../../service/getData'
export default {
    data () {
        return {
            warning:true,
            inputAdress:'',
            adresslist:[]
        }
    },components: {
        headTop
    },computed: {
        ...mapState([
            'userInfo','addAddress'
        ])
    },methods: {
        ...mapMutations([
            'SAVE_ADDDETAIL'
        ]),
        inputThing()
        {
            searchNearby(this.inputAdress).then(res=>{
                this.adresslist=res;
                this.warning=true;
                if(this.adresslist.length>0)
                {
                    this.warning=false;
                    if(this.inputAdress=='')
                    {
                        this.adresslist=[];
                        this.warning=true;
                    }
                }
            })
        },
        //把选择的地址name存入vuex
        listClick(index)
        {
            this.SAVE_ADDDETAIL(this.adresslist[index].name);
            this.$router.go(-1);
        }
    },created () {
        this.inputAdress=this.addAddress ? this.addAddress :this.inputAdress;
    }
}
</script>

<style lang="scss" scopde>
@import '../../../../../../style/mixin.scss';
.addDetail-container{
    position: fixed;
    top:0;
    left:0;
    right: 0;
    bottom: 0;
    background: #f5f5f5;
    z-index:260;
    p, span{
        font-family: Helvetica Neue,Tahoma,Arial;
    }
    overflow-y: auto;
}
section{
    .add-detail{
        margin-top: 2.3rem;
        background: #fff;
        padding:0.5rem;
        display:flex;
        input{
            flex:6;
            background: #f2f2f2;
            border:1px solid #ddd;
            height: 1.7rem;
            border-radius: 0.2rem;
            padding:0.2rem;
            @include sc(0.7rem,#333);
        }
        button{
            margin-left:0.3rem;
            @include sc(0.7rem,#fff);
            flex:1.8;
            background: $blue;
            border-radius: 0.2rem;
        }
    }
    .warnpart{
        @include sc(0.6rem,#FF883F);
        text-align: center;
        background: #FFF6E4;
        padding:0.4rem;
    }
    .point{
        @include center;
        width: 100%;
      p{
          margin:auto;
          text-align: center;
          @include sc(0.6rem,#666);
          padding:0.3rem;
      }
    }
}
.poisearch-container{
    padding:0.5rem;
    li{
        p:nth-of-type(1){
            @include sc(0.7rem,#666);
        }
        p:nth-of-type(2)
        {
            padding-top: 0.2rem;
             @include sc(0.554rem,#666);
        }
        padding:0.3rem;
        border-bottom: 0.025rem solid #ddd;
    }
}
</style>



